.card{
    width: 400px;
    height: 475px;
    margin-left: 40px;
    margin-right: 40px; 
    margin: 20px; 
    /* display: flex;
    flex-direction: row; */
}
.card-img{
    width: auto;
    max-width: 400px;
    height:auto;
    max-height: 275px;
    min-height: 275px;
}
.card-body{
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    font-size: 18px;
    order: 1;
}

.card-title{
    font-size: 22px;
    text-align: center;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.card-subtitle{
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

